<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png" />
    <p>vue - 第一个子应用</p>
    <!-- <el-button type="success" plain @click="callParent">传参给父应用</el-button> -->
    <p>{{ data.msg }}</p>
    <!-- <p>{{ data.msgs }}</p> -->
  </div>
</template>

<script>
// @ is an alias to /src
import actions from '@/shared/actions'

export default {
  name: 'Home',
  data() {
    return {
      data: {
        msg: ''
        // msgs: ''
      }
    }
  },
  mounted() {
    // 注册观察者函数
    // onGlobalStateChange 第二个参数为 true，表示立即执行一次观察者函数
    actions.onGlobalStateChange((state, prevState) => {
      // state: 变更后的状态; prevState: 变更前的状态
      console.log('1子应用观察者：data 改变前的值为 ', prevState)
      console.log('1子应用观察者：data 改变后的值为 ', state)
      this.data.msg = state.msg
    }, true)
  },
  methods: {
    callParent() {
      // this.data.msgs = '向父组件传参 - ' + Math.round(Math.random() * 100)
      // //设置 token
      // actions.setGlobalState(this.data)
    }
  }
}
</script>
